:root {
  --font-primary: "Rubik", sans-serif;
}

:root {
  --color-bg-body: hsl(226, 43%, 10%);
  --color-bg-card: hsl(235, 46%, 20%);
  --color-report: hsl(236, 100%, 87%);
  --color-contrast: hsl(235, 45%, 61%);
  --color-bg-report: hsl(246, 80%, 60%);
  --color-bg-work: hsl(15, 100%, 70%);
  --color-bg-play: hsl(195, 74%, 62%);
  --color-bg-study: hsl(348, 100%, 68%);
  --color-bg-exercise: hsl(145, 58%, 55%);
  --color-bg-social: hsl(264, 64%, 52%);
  --color-bg-self-care: hsl(43, 84%, 65%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background-color: var(--color-bg-body);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 88vw;
  max-width: 68.75rem;
}

.cards {
  display: grid;
  gap: 1rem;
  grid-template-columns: 100%;
}

.report {
  background-color: var(--color-bg-card);
  border-radius: 1rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
}
.report__user {
  background-color: var(--color-bg-report);
  border-radius: 1rem;
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
}
.report__image {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 50%;
  border: 3px solid #fff;
}
.report__text {
  color: #fff;
  font-size: 1.5rem;
}
.report__text span {
  display: block;
  color: var(--color-report);
  font-size: 1rem;
  line-height: 1.5;
}
.report__list {
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.report__btn {
  border: none;
  background: transparent;
  color: var(--color-contrast);
  font-weight: 500;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all ease 0.5s;
}
.report__btn:hover, .report__btn--active {
  color: #fff;
}

.card {
  background-color: var(--color-bg-work);
  border-radius: 1rem;
  background-image: url(/images/icon-work.svg);
  background-repeat: no-repeat;
  background-position: 92% -1rem;
}
.card__content {
  background-color: var(--color-bg-card);
  margin-top: 2.25rem;
  border-radius: 1rem;
  padding: 1.75rem;
  cursor: pointer;
  transition: all ease 0.5s;
}
.card__content:hover {
  opacity: 0.9;
}
.card__assignment, .card__date {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card__assignment {
  margin-bottom: 0.5rem;
}
.card__subtitle, .card__hours {
  color: #fff;
}
.card__subtitle {
  font-size: 1rem;
  font-weight: 500;
}
.card__hours {
  font-size: 2rem;
  font-weight: 300;
}
.card__description {
  font-size: 0.95rem;
  color: var(--color-report);
}